<template>
  <div>
    <div class="activesDetail_title"></div>
    <div class="activesDetail">
      <el-table
        class="activesDetail_table"
        :header-cell-style="{background:'#494949',color:'#ffffff'}"
        :data="activesDetailData"
        tooltip-effect="dark"
        style="width: 100%">
        <el-table-column
          align="center"
          label=""
          width='380'>
          <template slot-scope="scope">
            <span style="color:#B0AFAF">{{scope.row.name}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="">
          <template slot-scope="scope">
            {{scope.row.status}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="">
          <template slot-scope="scope">
            {{scope.row.date}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="">
          <template slot-scope="scope">
            {{scope.row.lastDate}}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="">
          <template slot-scope="scope">
            {{scope.row.content}}
          </template>
        </el-table-column>
      </el-table>
    </div>
    <router-link to="/manager/actives">
      <div class="activesDetail_bottom"></div>
    </router-link>
  </div>
</template>

<script>
export default {
    data () {
      return {
        activesDetailData:[{
          name:'',
          status:'29',
          content:'2',
          date:'2017-12-102019-09-10',
          lastDate:'2017-12-102019-09-10'
        },{
          name:'',
          status:'29',
          content:'2',
          date:'2017-12-102019-09-10',
          lastDate:'2017-12-102019-09-10'
        }]
      }
    },
    methods:{

    },
    computed: {
      
    },
    components: {
      
    },
    created(){
      this.$stamp(null,this.$route.params.data)
    }
}
</script>

<style>
  .activesDetail_title{
    line-height:40px;
    font-weight:bold;
    width:100%;	
    color:#ffffff;
    padding-left:15px;
    box-sizing:border-box;
    background-color: #ed6f00;
	  border-radius: 5px 5px 0px 0px;
  }
  .activesDetail_bottom{
    padding:5px 15px;
    box-sizing:border-box;
    background-color: #ed6f00;
	  border-radius: 30px;
    width:100px;
    margin:15px auto;
    color:#ffffff;
    text-align:center;
    cursor:pointer;
  }
</style>



